import { ajax } from "../core/ajax";
import { me } from "../core/event";
class Project {
    public plist: Array<any> = [];
    /**
     * 获取项目列表
     */
    public async getProjectList() {
        let obj: any = {};
        this.checkRight(obj);
        let list = await ajax.postAync("/projectlist", obj, undefined, () => {
            return [{
                title: '测试项目1',
                info: '233333333',
                list: [{ name: '111', list: [] }],
                users: ['Dusk_Y']
            }, {
                title: '测试项目2',
                info: '233333333',
                list: [{ name: '222', list: [] }],
                users: ['Dusk_Y']
            }, {
                title: '测试项目3',
                info: '233333333',
                list: [{ name: '333', list: [] }],
                users: ['Dusk_Y']
            }];
        });

        for (let i = 0; i < list.length; i++) {
            let p = list[i];
            p.list = p.list.filter((a: any) => {
                return a.finish == 0 || a.finish == null;
            })
            p.idx = i;
            p.fnum = 0;
            p.anum = 0;
            for (let j = 0; j < p.list.length; j++) {
                let g = p.list[j];
                g.idx = j;
                g.fnum = 0;
                for (let k = 0; k < g.list.length; k++) {
                    let c = g.list[k];
                    c.idx = k;
                    c.gid = g.gid;
                    c.pid = p.pid;
                    if (c.finish) {
                        g.fnum++;
                        p.fnum++;
                    }
                    p.anum++;
                }
                g.pid = p.pid;
            }
        }
        this.plist = list;
        me.Event.emit("projectupdate");

    }

    /**
     * addProject
     * 添加新项目
     */
    public async addProject(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/addproject", obj);
    }

    /**
     * updateProject
     * 更新项目信息
     */
    public async updateProject(obj: any) {
        this.checkRight(obj);
        await ajax.postAync("/updateproject", obj);
    }

    /**
     * deleteProject
     * 删除项目
     */
    public async deleteProject(obj: any) {
        this.checkRight(obj);
        await ajax.postAync("/deleteproject", obj);
    }

    /**
     * addGroup
     * 添加分组
     */
    public async addGroup(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/addprojectgroup", obj, undefined, () => {
            return { gid: (10000 * Math.random()).toString(), list: [] }
        });
    }

    /**
     * updateGroup
     * 添加分组
     */
    public async updateGroup(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/updateprojectgroup", obj, undefined, () => {
            return { gid: (10000 * Math.random()).toString(), list: [] }
        });
    }

    /**
     * deleteGroup
     * 删除分组
     */
    public async deleteGroup(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/deleteprojectgroup", obj);
    }

    /**
     * addCard
     * 添加卡片
     */
    public async addCard(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/addcard", obj, undefined, () => {
            return { cid: (10000 * Math.random()).toString() }
        });
    }

    /**
     * updateCard
     * 更新卡片信息
     */
    public async updateCard(obj: any) {
        this.checkRight(obj);
        return await ajax.postAync("/updatecard", obj);
    }

    private checkRight(obj: any) {
        obj.username = sessionStorage.getItem("username");
        obj.token = sessionStorage.getItem("token");
    }
}
export let project = new Project();


