







import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
@Component({
  components: {}
})
export default class Test extends Vue {
  @Prop() title!: string;
  @Prop() value!: any;
  get valueString(): any {
    return JSON.stringify(this.value, null, 2);
  }
  set valueString(v: any) {
    this.value = v;
  }
}



