import { ajax } from "../core/ajax";
class User {
    public info: any;

    /**
     * getUserInfo
     */
    public async getUserInfo(username: string, password: string) {
        return await ajax.postAync("/login", { username: username, password: password }, undefined, () => {
            return {
                nickName: '测试用户',
                token: '233'
            };
        });
    }

}
export let user = new User();


