



















import { Component, Vue, Prop } from "vue-property-decorator";
import { project } from "../../server/project";
import { me } from "../../core/event";
import Alert from "../common/Alert.vue";
@Component({
  components: {}
})
export default class ProjectList extends Vue {
  // @Prop(Object) project!: any;
  Project: any = {
    list: []
  };
  select(item: any) {
    me.Event.emit("select", item);
  }
  mounted() {
    me.Event.on("projectupdate", this.update);
  }
  update() {
    // this.Project.list = project.plist;
    this.$set(this.Project, "list", project.plist);
  }
  addProject() {
    me.Event.emit("showalert", {
      title: "创建项目",
      info: "是否确认新建项目？",
      list: {
        title: { name: "项目名字", value: "" },
        info: { name: "项目介绍", value: "" }
      },
      okCall: async (rd: any) => {
        let obj: any = {
          title: rd.list.title.value,
          info: rd.list.info.value,
          list: [],
          users: [sessionStorage.getItem("username")]
        };
        let p: any = await project.addProject(obj);
        obj.idx = this.Project.list.length;
        obj.pid = p.pid;
        obj.fnum = 0;
        obj.anum = 0;
        project.plist.push(obj);
        this.update();
      }
    });
  }
}



