













































































import Vue from "vue";
import Component from "vue-class-component";
import { me } from "../../core/event";
import { project } from "../../server/project";
import Test from "../common/Test.vue";
import { Prop } from "vue-property-decorator";
import Card from "./Card.vue";
import draggable from "vuedraggable";
@Component({
  components: { Card, Test, draggable }
})
export default class extends Vue {
  list: any = [];
  setting: number = -1;
  project: any = { selected: 0, pid: 0 };
  mounted() {
    me.Event.on("select", this.select);
    me.Event.on("finishcard", this.onFinishCard);
  }
  select(d: any) {
    console.log("change project " + d.idx);
    this.project.selected = d.idx;
    this.project.pid = d.pid;
    this.list = project.plist[d.idx].list;
    console.log(this.list);
  }
  onFinishCard(d: any) {
    // let idx = this.list.findIndex((item: any, ii: any, arr: any) => {
    //   return item.gid == d.gid;
    // });
    this.list = project.plist[this.project.selected].list;
  }
  //拖拽卡片
  onCardEnd = async (event, item) => {
    console.log("onCardEnd");
    let list = project.plist[this.project.selected].list;
    let from = item.gid;
    let to = event.to.attributes[1].value;
    let fromlist: Array<any> = [];
    let tolist: Array<any> = [];
    let fromidx = list.findIndex((iii: any, ii: any, arr: any) => {
      return iii.gid == from;
    });
    let from_finish_num = 0;
    for (let i = 0; i < list[fromidx].list.length; i++) {
      let c = list[fromidx].list[i];
      c.finish && from_finish_num++;
      fromlist.push(c.cid);
    }
    // let cid = list[event.oldIndex];
    if (to != from) {
      await project.updateGroup({ gid: from, list: fromlist });
      list[fromidx].fnum = from_finish_num;

      let toidx = list.findIndex((iii: any, ii: any, arr: any) => {
        return iii.gid == to;
      });

      let to_finish_num = 0;
      for (let i = 0; i < list[toidx].list.length; i++) {
        let c = list[toidx].list[i];
        c.finish && to_finish_num++;
        list[toidx].list[i].gid = to;
        tolist.push(c.cid);
      }
      await project.updateGroup({ gid: to, list: tolist });

      list[toidx].fnum = to_finish_num;
      this.list = list;
    } else {
      project.updateGroup({ gid: from, list: fromlist });
    }
  };
  //拖拽分组
  async onGroupEnd(event) {
    let list = project.plist[this.project.selected].list;
    let gidlist: Array<any> = [];
    for (let i = 0; i < list.length; i++) {
      let group = list[i];
      gidlist.push(group.gid);
    }
    await project.updateProject({ pid: this.project.pid, list: gidlist });
  }
  //添加分组
  addList = async (name = "新分组") => {
    let newgroup: any = {
      name: name,
      list: [],
      pid: this.project.pid,
      idx: project.plist[this.project.selected].list.length
    };
    let rd = await project.addGroup(newgroup);
    newgroup.gid = rd.gid;
    newgroup.fnum = 0;
    // this.list.push(newgroup);
    project.plist[this.project.selected].list.push(newgroup);
    this.list = project.plist[this.project.selected].list;
  };
  setGroup = async (obj: any) => {
    let key = obj[0];
    let value = obj[1];
    if (key == "delete") {
      await project.deleteGroup(value);
      let index = project.plist[this.project.selected].list.findIndex(
        (item: any, ii: any, arr: any) => {
          return item.gid == value.gid;
        }
      );
      let num = project.plist[this.project.selected].list[index].list.length;
      project.plist[this.project.selected].fnum -=
        project.plist[this.project.selected].list[index].fnum;
      project.plist[this.project.selected].list.splice(index, 1);
      this.list = [];
      this.list = project.plist[this.project.selected].list;
      project.plist[this.project.selected].anum -= num;
      me.Event.emit("finishcard", {
        gid: value.gid,
        pid: value.pid
      });
    } else if (key == "finish") {
      await project.updateGroup({ gid: value.gid, finish: 1 });
      let index = project.plist[this.project.selected].list.findIndex(
        (item: any, ii: any, arr: any) => {
          return item.gid == value.gid;
        }
      );
      let num = project.plist[this.project.selected].list[index].list.length;
      project.plist[this.project.selected].fnum -=
        project.plist[this.project.selected].list[index].fnum;
      project.plist[this.project.selected].list.splice(index, 1);
      this.list = [];
      this.list = project.plist[this.project.selected].list;
      project.plist[this.project.selected].anum -= num;
      me.Event.emit("finishcard", {
        gid: value.gid,
        pid: value.pid
      });
    }
  };
  sortGroupIdx(olist: any) {
    let list: any = [];
    // let olist = project.plist[this.project.selected].list;
    for (let j = 0; j < olist.length; j++) {
      let g = olist[j];
      g.idx = j;
      list.push(g);
    }
    return list;
    // project.plist[this.project.selected].list = list;
  }
  //添加卡片
  addCard = async (group: any) => {
    let card: any = {
      title: "新任务",
      info: "",
      gid: group.gid,
      pid: this.project.pid,
      idx: group.list.length
    };
    let rd = await project.addCard(card);
    card.canEdit = false;
    card.cid = rd.cid;
    card.finish = 0;
    card.mark = [];
    let index = project.plist[this.project.selected].list.findIndex(
      (item: any, ii: any, arr: any) => {
        return item.gid == group.gid;
      }
    );
    project.plist[this.project.selected].list[index].list.push(card);
    this.$set(
      this.list,
      index,
      project.plist[this.project.selected].list[index]
    );
    project.plist[this.project.selected].anum++;
    project.plist[this.project.selected].list[index].anum++;
    me.Event.emit("finishcard", {
      gid: group.gid,
      pid: group.pid
    });
  };
  saveTitle = async (item: any, index: number) => {
    let title = (this.$refs["title" + index] as any)[0].innerText;
    await project.updateGroup({ gid: item.gid, name: title });
    project.plist[this.project.selected].list.name = title;
  };
  dataDetails(index: number) {
    this.setting = index;
  }
  hiddenDetail() {
    this.setting = -1;
  }
}



