import { project } from "../../server/project";
import { me } from "../../core/event";
@Component({
  components: {}
})
export default class Card extends Vue {
  db = false;
  @Prop() _card!: any;
  card: any = { title: "", info: "", cid: "" };
  inputVisible: boolean = false;
  inputValue: String = "";
  canEdit = false;
  async changeText(focus = true) {
    let card = this.card;
    card.title = (this.$refs.title as any).innerText;
    card.info = (this.$refs.info as any).innerText;
    await project.updateCard(card);
    card.focus = focus;
    this.updateCard(card);
  }
  changeMode() {
    if (this.canEdit) {
      this.changeText(false);
    }
    this.canEdit = !this.canEdit;
  }
  async finisheCard(finish: boolean) {
    setTimeout(() => {
      this.db = false;
      clearTimeout();
    }, 500);
    if (!this.db) {
      this.db = !this.db;
      return;
    }
    let card = this.card;
    card.finish = finish;
    await project.updateCard({ finish: finish ? 1 : 0, cid: card.cid });
    console.log(card);
    this.updateCard(card, finish ? 1 : -1);
    me.Event.emit("finishcard", {
      gid: card.gid,
      pid: card.pid
    });
  }

  updateCard(card, finish: any = null) {
    this.card = card;
    let pidx = project.plist.findIndex((item: any, ii: any, arr: any) => {
      return item.pid == card.pid;
    });
    let gidx = project.plist[pidx].list.findIndex(
      (item: any, ii: any, arr: any) => {
        return item.gid == card.gid;
      }
    );
    let idx = project.plist[pidx].list[gidx].list.findIndex(
      (item: any, ii: any, arr: any) => {
        return item.cid == card.cid;
      }
    );
    project.plist[pidx].list[gidx].list[idx] = card;
    if (finish != null) {
      project.plist[pidx].fnum += finish;
      project.plist[pidx].list[gidx].fnum += finish;
    }
  }

  showInput() {
    this.inputVisible = true;
    this.$nextTick(() => {
      (this.$refs.saveTagInput as any).$refs.input.focus();
    });
  }
  async handleInputConfirm() {
    let inputValue = this.inputValue;
    if (inputValue) {
      let card = this.card;
      card.mark.push(inputValue);
      await project.updateCard({
        cid: card.cid,
        mark: card.mark
      });
      this.updateCard(card);
    }
    this.inputVisible = false;
    this.inputValue = "";
  }
  async removeMark(index: number) {
    let card = this.card;
    card.mark.splice(index, 1);
    await project.updateCard({
      cid: card.cid,
      mark: card.mark
    });
    this.updateCard(card);
  }
  mounted() {
    this.card = this._card;
  }
}



