






















































import Vue from "vue";
import Component from "vue-class-component";
import { user } from "../../server/user";
import ProjectList from "./ProjectList.vue";
import CardList from "./CardList.vue";
import { project } from "../../server/project";
import { me } from "../../core/event";
@Component({
  components: { ProjectList, CardList }
})
export default class ProjectBoard extends Vue {
  username: string = "";
  password: string = "";
  nickName: string = "";
  islogin: boolean = false;
  plist: any = [];
  select: string = "";
  selectIdx: number = 0;
  aim: string = "";
  isLogin() {
    if (
      sessionStorage.getItem("username") == "" ||
      // sessionStorage.getItem("token") == "" ||
      !sessionStorage.getItem("username")
      // !sessionStorage.getItem("token")
    ) {
      return false;
    } else {
      return true;
    }
  }
  main() {
    // this.plist = project.plist;
    this.select = "";
    this.aim = "";
    this.$nextTick(() => {
      me.Event.emit("projectupdate");
    });
  }
  async login() {
    let d = await user.getUserInfo(this.username, this.password);
    if (!d) return;
    sessionStorage.setItem("username", this.username.toString());
    sessionStorage.setItem("nickName", d.nickName);
    sessionStorage.setItem("token", d.token);
    this.islogin = this.isLogin();
    this.username = sessionStorage.getItem("username") || "";
    this.nickName = sessionStorage.getItem("nickName") || "";
    this.getList();
  }
  async quit() {
    sessionStorage.setItem("username", "");
    sessionStorage.setItem("nickName", "");
    sessionStorage.setItem("token", "");
    this.islogin = this.isLogin();
    project.plist = [];
    me.Event.cut("select", this.selectProject);
  }
  mounted() {
    this.islogin = this.isLogin();
    if (this.islogin) {
      this.username = sessionStorage.getItem("username") || "";
      this.nickName = sessionStorage.getItem("nickName") || "";
      this.getList();
      me.Event.on("finishcard", this.onFinishCard);
    }
  }
  async getList() {
    await project.getProjectList();
    this.plist = project.plist;
    me.Event.on("select", this.selectProject);
  }

  selectProjectByMenu(d: any) {
    me.Event.emit("select", d);
  }

  selectProject(d: any) {
    console.log(d.idx);
    this.select = this.plist[d.idx].title;
    this.selectIdx = d.idx;
    let plist = project.plist;
    this.aim =
      "当前项目总计任务：" + plist[d.idx].fnum + "/" + plist[d.idx].anum;
  }
  async onFinishCard() {
    if (this.select == "") {
      return;
    }
    let plist = project.plist;
    this.aim =
      "当前项目总计任务：" +
      plist[this.selectIdx].fnum +
      "/" +
      plist[this.selectIdx].anum;
  }
}



